<template>
  <div class="dashboard-table">
    <div class="table-container">
      <h1
        v-if="title"
        class="title py-2 px-4"
      >
        {{ title }}
      </h1>
      <table class="table">
        <thead>
          <tr>
            <th
              v-for="(header, index) in headers"
              :key="index"
              :class="header.class"
              class="py-4 px-2 font-weight-normal"
              :width="header.width"
            >
              {{ header.text }}
            </th>
          </tr>
          <tr v-if="loading">
            <th :colspan="headers.length" class="progress">
              <v-progress-linear
                indeterminate
              ></v-progress-linear>
            </th>
          </tr>
        </thead>
        <tbody>
          <tr 
            v-for="(item, itemIndex) in items"
            :key="itemIndex"
          >
            <td 
              v-for="(header, headerIndex) in headers"
              :key="headerIndex"
              :class="header.class"
              class="py-2 px-4"
            >
              <template v-if="header.type === 'image'">
                <div :style="{ background: `url(${getImage(item[header.value])})`, backgroundSize: 'contain' }" class="image"></div>
              </template>
              <template v-else-if="header.type === 'list'">
                <ul>
                  <li
                    v-for="(item, i) in item[header.value]"
                    :Key="i"
                  >
                    {{ item }}
                  </li>
                </ul>
              </template>
              <template v-else-if="header.type === 'action'">
                <v-btn
                  v-for="(action, i) in item[header.value]"
                  :key="i"
                  :class="action.class"
                  class="actionButton my-3 my-sm-2 mx-1 elevation-xs-0 elevation-sm-1"
                  @click="callAction(action.action, item)"
                >
                  {{ action.text }}
                </v-btn>
              </template>
              <template v-else>
                {{ item[header.value] }}
              </template>
            </td>
          </tr>
          <tr v-if="items.length === 0">
            <td 
              class="pa-4 text-center"
              :colspan="headers.length"
            >
              <span v-if="!loading">{{ noResultText }}</span>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <v-pagination
      v-if="!noPagination"
      class="mt-8"
      v-model="page"
      :length="qtdPages"
      :total-visible="8"
      color="primary"
      @input="changePage"
    />
  </div>
</template>

<script>
export default {
  name: 'DashboardTable',
  props: {
    headers: { 
      type: Array,
      default: () => [] 
    },
    items: { 
      type: Array,
      default: () => [] 
    },
    loading: { default: false },
    loadingText: { default: 'Aguarde... carregando' },
    noPagination: { default: false },
    noResultText: { default: 'Nenhum registro encontrado' },
    page: { default: null },
    qtdPages: { default: null },
    title: { default: null }
  },
  methods: {
    callAction(action, item) {
      this.$emit('callAction', action, item)
    },
    changePage (page) {
      this.page = page
      this.$emit('changePage', page)
    },
    getImage (image) {
      return `${this.$url_backend}${image}`
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/scss/mixin.scss';
.table-container {
  background: #fff;
  border-radius: 15px;
  overflow: hidden;
  width: 100%;
  overflow-x: auto;
}
.title { font-weight: 600; color: #4F4F4F; }
.image { background-color: #ccc !important; }
table { width: 100%; border-spacing: 0;  }
th { color: #919191; }
thead tr:last-child th { border-bottom: 2px solid #707070; }
td { border-bottom: 1px solid #ccc; }
tr:last-child td { border-bottom: none; }
.progress { border: none !important; }
.image { 
  width: 70px;
  overflow: hidden;
  border-radius: 50%;
  height: 70px;
}
ul { list-style-position: inside; padding-left: 0; }
.actionButton {
  border-radius: 45px;
  &.success { color: #4F4F4F; }
}
@media all and (max-width: 599px) {
  thead { display: none; }
  .table-container { background: transparent; }
  tr {
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
    border-radius: 15px;
    background: #fff;
    padding: 15px;
    @include box-shadown-mixin(.5);
  }
  td {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: none; 
    align-items: center;
  }
  .image { width: 150px; height: 150px; }
  .actionButton { height: 45px !important; width: 220px; }
}
</style>